<template>
  <div>
    <el-button @click="upload">上传风景</el-button>
    <el-table :data="scenrylist" height="410" border>
      <el-table-column prop="ts_id" label="风景ID" />
      <el-table-column prop="ts_url" label="风景图片">
        <template slot-scope="scope">
          <img :src="root + scope.row.ts_url" width="45" height="45" />
        </template>
      </el-table-column>
      <el-table-column prop="ts_descript" label="风景介绍" />
      <el-table-column label="操作">
        <div slot-scope="scope">
          <el-button title="添加服务" type="primary" size="mini" icon="iconfont icon-jiahao" @click="addserver(scope.row)" />
          <el-button title="修改" type="warning" size="mini" icon="iconfont icon-shenqing" @click="alert(scope.row)" />
        </div>
      </el-table-column>
    </el-table>
    <el-dialog style="margin-top: 2vh;" title="发布商品" :visible.sync="dialogVisible1" width="40%" :before-close="handleClose1">
      <div>
        <el-form label-width="70" :model="addform">
          <el-form-item label="风景图片">
            <el-upload
              ref="addformref"
              :limit="1"
              method="POST"
              :action="root + 'addScenery'"
              name="file"
              :on-success="success"
              :data="addform"
              list-type="picture-card"
              :auto-upload="false"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="风景介绍">
            <el-input type="textarea" v-model="addform.ts_descript"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose1">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible1: false,
      addform: {},
      scenrylist: []
    }
  },
  created() {
    this.getscenery()
  },
  methods: {
    async getscenery() {
      const { data: res } = await this.$http.get("scenery/showAll")
      if (res.code !== 200) return
      this.scenrylist = res.obj
    },
    upload() {
      this.dialogVisible1 = true
    },
    submit() {
      this.$refs.addformref.submit()
    },
    success() {
      this.getscenery()
      this.handleClose1()
    },
    handleClose1() {
      this.dialogVisible1 = false
    }
  }
}
</script>
