<template>
  <div>
    <el-button @click="add">上传资讯</el-button>
    <el-table :data="infolist" height="410" border>
      <el-table-column prop="ti_id" label="资讯ID" />
      <el-table-column label="资讯标题">
        <template slot-scope="scope">
          <p class="infocantent">{{ scope.row.ti_title }}</p>
        </template>
      </el-table-column>
      <el-table-column prop="ti_createTime" label="上传时间" />
      <el-table-column label="操作">
        <div slot-scope="scope">
          <el-button title="添加服务" type="primary" size="mini" icon="iconfont icon-jiahao" @click="addserver(scope.row)" />
          <el-button title="修改" type="warning" size="mini" icon="iconfont icon-shenqing" @click="alert(scope.row)" />
        </div>
      </el-table-column>
    </el-table>
    <el-dialog style="margin-top: 2vh;" title="发布商品" :visible.sync="dialogVisible1" width="40%" :before-close="handleClose1">
      <div>
        <el-form label-width="70" :model="addform">
          <el-form-item label="资讯标题">
            <el-input v-model="addform.ti_title"></el-input>
          </el-form-item>
          <el-form-item label="资讯内容">
            <el-input type="textarea" v-model="addform.ti_context"></el-input>
          </el-form-item>
          <el-form-item label="风景图片">
            <el-upload
              ref="addformref"
              multiple
              :action="root + 'addInformation'"
              name="file"
              :on-success="success"
              :data="addform"
              list-type="picture-card"
              :auto-upload="false"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose1">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible1: false,
      addform: {},
      infolist: []
    }
  },
  created() {
    this.getinfo()
  },
  methods: {
    async getinfo() {
      const { data: res } = await this.$http.get("info/showAll")
      console.log(res)
      if (res.code !== 200) return
      this.infolist = res.obj
      console.log(this.infolist)
    },
    add() {
      this.dialogVisible1 = true
    },
    submit() {
      this.$refs.addformref.submit()
    },
    success(res) {
      console.log(res)

      this.getinfo()
      this.dialogVisible1 = false
    },
    handleClose1() {
      this.dialogVisible1 = false
    }
  }
}
</script>
<style lang="less" scoped>
.infocantent {
  width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>
